<template>
    <v-container fluid fill-height>
        <v-row>
            <v-card
                    class="col-8 mx-auto"
                    raised
                    shaped
            >
                <v-card-text>
                    <p class="display-1 text--primary">Sprawd status zgoszenia</p>

                    <v-form v-model="valid">
                        <v-text-field
                                v-model="token"
                                :rules="rules.ticket.token"
                                :counter="15"
                                label="Numer zgoszenia"
                                required
                        />
                    </v-form>

                    <v-alert
                            border="left"
                            colored-border
                            :color="status.color"
                            elevation="2"
                            v-if="!!status"
                    >
                        Status zgoszenia: {{status.name}}
                    </v-alert>

                    <loading v-if="loading"/>
                </v-card-text>

                <v-card-actions>
                    <v-btn
                            text
                            class="mx-auto font-weight-bold"
                            @click="checkStatus"
                            :disabled="!valid"
                    >
                        Sprawd status
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-row>
    </v-container>
</template>

<script>
    import Loading from "./../Helpers/Loading"

    export default {
        name: 'CheckStatus',
        components:{
            Loading
        },
        data: () => ({
            valid: false,
            token: '',
            loading: false,
            status: null,
            rules: {
                ticket: {
                    token: [
                        v => !!v || 'Numer zgoszenia jest wymagany.',
                        v => v.length === 15 || 'Numer zgoszenia skada si z 15 znakw.'
                    ],
                }
            },
        }),
        methods: {
            checkStatus() {
                this.loading = true;
                this.status = null;

                this.$http.get(`/api/ticket/${this.token}/status`).then((response) => {
                    this.status = response.data.data.status
                    this.loading = false;
                });
            },
        }
    }
</script>

