<template>
    <span>
        <note-form ref="createForm" :ticket_id="ticket_id" @valid="checkValid"/>

        <v-btn block class="mb-3" :disabled="!valid" @click="addNote()">
            Dodaj notatk
        </v-btn>
    </span>
</template>

<script>
    import NoteForm from "./Form"

    export default {
        name: 'NoteCreate',
        props:{
          ticket_id: {}
        },
        components: {
            NoteForm
        },
        data: () => ({
            valid: false,
        }),
        methods: {
            async addNote() {
                this.$http.post('/api/note', this.$refs.createForm.credentials)
                    .then(() => {
                        this.$toasted.show('Dodano notatk.', {
                            type: 'success'
                        });
                    })
            },

            checkValid(value) {
                this.valid = value
            }
        },
    }
</script>

