<template>
    <v-list dense shaped>
        <v-list-item v-if="isManager() && currentUser.agency_id" ripple link
                     :to="{name: 'AgencySummary', params: { id: currentUser.agency_id }}"
                     class="route"
                     active-class="item-active"
                     exact>
            <v-list-item-action>
                <font-awesome-icon icon="toolbox" size="lg"/>
            </v-list-item-action>
            <v-list-item-content>
                <v-list-item-title>
                    Oddzia
                </v-list-item-title>
            </v-list-item-content>
        </v-list-item>

        <span v-for="category in categories" :key="category.name">
            <v-subheader v-if="categoryAvailable(category)" class="mt-4 white--text font-weight-bold text--darken-1">{{category.name}}</v-subheader>
            <span v-for="link in category.links" :key="link.route">
                <v-list-item v-if="checkPermitted(link.route)" ripple link :to="{name: link.route}" class="route"
                             active-class="item-active"
                             exact>
                    <v-list-item-action>
                        <font-awesome-icon :icon="link.icon" size="lg"/>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>
                            {{link.label}}
                        </v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </span>
        </span>
    </v-list>
</template>

<script>
    export default {
        data: () => ({
            drawer: null,
            applicationName: 'Service Management System',
            logged: true,
            categories: [
                {
                    name: 'Gwne', available: 0, links: [
                        {route: 'HomePage', label: 'Strona gowna', icon: 'home',},
                    ]
                },
                {
                    name: 'Zgoszenia', available: 0, links: [
                        {route: 'TicketCheckStatus', label: 'Sprawd status zgoszenia', icon: 'clipboard-check',},
                        {route: 'TicketsList', label: 'Lista zgosze', icon: 'clipboard-list',},
                        {route: 'TicketCreate', label: 'Dodaj zgoszenie', icon: 'plus',},
                    ]
                },
                {
                    name: 'Klienci', available: 0, links: [
                        {route: 'ClientList', label: 'Lista klientw', icon: 'users',},
                        {route: 'ClientCreate', label: 'Dodaj klienta', icon: 'user-plus',},
                    ]
                },
                {
                    name: 'Urzdzenia', available: 0, links: [
                        {route: 'DeviceList', label: 'Lista urzdze', icon: 'laptop',},
                        {route: 'DeviceCreate', label: 'Dodaj urzdzenie', icon: 'laptop-medical',},
                    ]
                },
                {
                    name: 'Uytkownicy', available: 0, links: [
                        {route: 'UsersList', label: 'Lista uytkownikw', icon: 'users',},
                        {route: 'UserCreate', label: 'Dodaj uytkownika', icon: 'user-plus',},
                    ]
                },
                {
                    name: 'Oddziay', available: 0, links: [
                        {route: 'AgencyList', label: 'Lista oddziaw', icon: 'city',},
                        {route: 'AgencyCreate', label: 'Dodaj oddzia', icon: 'clinic-medical',},
                    ]
                },
            ]
        }),
        methods: {
            checkPermitted(routeName) {
                if (this.$router.resolve({name: routeName}).route.meta.roles) {
                    if (this.$store.state.currentUser) {
                        return this.$router.resolve({name: routeName}).route.meta.roles.includes(this.$store.state.currentUser.role.name);
                    }
                    return false;
                }
                return true;
            },
            categoryAvailable(category) {
                let count = 0;

                category.links.forEach((item) => {
                    if (this.checkPermitted(item.route)) {
                        count++;
                    }
                });

                return count;
            }
        },
    }
</script>

<style scoped>
    .item-active {
        background-color: #48614b;
    }
</style>